<template>
  <v-card>
    <v-card-title>
      <h3> {{this.title}} </h3>
    </v-card-title>
    <hr style="border: gray dashed 1px; transform: scaleY(0.5)">
    <v-card-text class="pa-0">
      <v-data-table :items="content" hide-default-footer>
        <template v-slot:body="{ items }">
          <tbody>
            <tr v-for="(header, idx) in headers" v-bind:key="idx">
              <td style="width:140px; font-weight:bold; background-color:#e0e0e0; ">
                {{ header.text }}
              </td>
              <td v-for="(item, idx) in items" v-bind:key="idx">
                {{ item[header.value] }}
              </td>
            </tr>
          </tbody>  
        </template>
      </v-data-table>
    </v-card-text>
  </v-card>
  
</template>

<script>
  export default {

    props: ['title', 'content'],
    data () {
      return {
        headers: [
          { text: '', value: 'target'},
          { text: '', value: 'age' },
          { text: '', value: 'amount' },
          { text: '', value: 'interestRate' },
          { text: '', value: 'pay' },
          { text: '', value: 'overdue' },
          { text: '', value: 'handling' },
          { text: '', value: 'extension' },
          { text: '', value: 'penalty' },
          { text: '', value: 'period' },
          { text: '', value: 'document' },
          { text: '', value: 'notice' },
        ]
      }
    },
  }
</script>

